@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Akaya+Telivigala&family=Roboto+Slab&display=swap);
body {
  margin: 0;
  font-family: 'Roboto Slab', serif, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.footer{
    background-color: #2e2e2e;
    color: #f10707;
    padding: 25px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.footer > p{
    font-size: 14px;
}

@media screen and (max-width: 650px){
    .footer > p{
        font-size: 10px;
    }
}
.searchForm__container{
    width: 80%;
    margin: 50px auto;
    background-color: #2e2e2e;
    color: aliceblue;
    padding: 30px 0;
}


.searchForm__container > h1{
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 100;
    font-size: 20px;
    margin-bottom: 30px;
}
    .searchForm__container > h1 > .icon {
        font-size: 30px;
        margin-right: 20px;
    }


.searchForm__container > form {
    display: flex;
    width: 100%;
    flex-direction: column;
}
    .searchForm__container > form > input{
        width: 97%;
        margin-left: auto;
        margin-right: auto;
        height: 35px;
        border-radius: 8px;
        border: none;
        padding-left: 20px;
    }
    .searchForm__container > form > button {
        height: 35px;
        width: 200px;
        margin-left: auto;
        margin-right: auto;
        margin-top: 30px;
        border-radius: 8px;
        outline: none;
        border: none;
        color: aliceblue;
        background-color: #f10707;
        font-size: 18px;
        cursor: pointer;
    }
    .searchForm__container > form > button:hover{
        background-color: aliceblue;
        color: #f10707;
        font-weight: 500;
    }

    @media screen and (max-width: 700px){
        .searchForm__container{
            margin: 30px auto;
        }
        .searchForm__container > h1{
            font-weight: 100;
            font-size: 15px;
        }
        .searchForm__container > h1 > .icon {
            font-size: 20px;
            margin-right: 10px;
        }
    }
.typical{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 60%;
    border: 1px solid #f10707;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 50px;
    padding: 40px 0;
}

.typical > .errorLink{
    margin-top: 30px;
    padding: 9px 18px;
    border-radius: 8px;
    outline: none;
    text-decoration: none;
    border: none;
    color: aliceblue;
    background-color: #f10707;
    font-size: 18px;
    cursor: pointer;
    text-align: center;
}
.errorMessage{
    width: 100%;
    margin: 60px auto;
    display: flex;
    justify-content: center;
    align-self: center;
    margin-bottom: 60px;
}
.moviesContainer{
    width: 80%;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 20px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 30px;
}

.moviesCard{
    background-color: #2e2e2e;
    height: 400px;
    padding-top: 30px;
}

.movieCard {
    width: 90%;
    height: 90%;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
}
.movieCard > .cardImage {
    width: 90%;
    height: 250px;
    margin-bottom: 10px;
    transition: all .2s ease-in-out;
}
.movieCard > .cardImage:hover{
    box-shadow: 0px 10px 15px rgba(255, 255, 255, 0.4);
    transform: scale(1.05);
}
.movieTitle{
    width: 90%;
    margin: auto 10px;
    color: aliceblue;
    font-size: 15px;
    font-weight: 300;
    /* border: 2px solid red; */
    height: 60px;
}

.cardLink{
    text-decoration: none;
    background-color: #f10707;
    padding: 8px 10px;
    color: aliceblue;
    border-radius: 7px;
    font-size: 14px;
}
.cardLink:hover{
    color: #f10707;
    background-color: aliceblue;
}


@media screen and (max-width: 1100px){
    .moviesContainer{
        grid-template-columns: 1fr 1fr 1fr;
    }
}

@media screen and (max-width: 830px){
    .moviesContainer{
        grid-template-columns: 1fr 1fr;
    }
}

@media screen and (max-width: 700px){
    .moviesContainer{
        grid-template-columns: 1fr;
    }
    .moviesCard{
        height: 450px;
    }
    .movieCard > .cardImage {
        height: 300px;
    }
}

.navbar{
    font-family: 'Akaya Telivigala', cursive;
    font-family: 'Roboto Slab', serif;
    display: flex;
    width: 100%;
    background-color: #2e2e2e;
    padding: 25px 100px;
    justify-content: space-between;
    align-items: center;
}
    .navbar > .navbarTitle{
        cursor: pointer;
        text-decoration: none;
    }

        .navbar > .navbarTitle > h1{
            color: aliceblue;
        }
        .navbar > .navbarIcon > .navbarIcon__movie {
            font-size: 40px;
            color: #f10707;
            cursor: pointer;
        }

@media screen and (max-width: 820px){
.navbar{
    padding: 25px;
}
    .navbar > .navbarTitle > h1{
        font-size: 20px;
    }

    .navbar > .navbarIcon > .navbarIcon__movie {
        font-size: 20px;
    }
}
/* http://www.omdbapi.com/?i=tt3896198&apikey=a82438f4 */

.movieInfoContainer{
    width: 80%;
    margin: 50px auto;
}

.movieInfoCard{
    width: 100%;
    display: flex;
}
.movieInfoCardOne{
    width: 33%;
    margin-right: 20px;
    border: 1px solid #2e2e2e;
    padding: 20px;
}   
    .movieInfoCardOne > img {
        width: 100%;
        height: 100%;
    }
.movieInfoCardTwo{
    width: 66%;
}
    .movieInfoCardTwo > h2 {
        margin-bottom: 20px;
    }
    .movieInfoCardTwo > ul {
        list-style-type: none;
    }
    .movieInfoCardTwo > ul > li{
        border: 1px solid #2e2e2e;
        padding: 8px;
        margin-bottom: 8px;
    }
    .movieInfoCardTwo > ul > li:last-child{
        margin-bottom: 0;
    }



    .movieAbout{
        border: 1px solid #2e2e2e;
        margin-top: 30px;
        padding: 20px;
    }
    .movieLinks{
        display: flex;
        justify-content: center;
    }
    .movieLinks > a, .movieLinks > Link{
        margin-top: 30px;
        padding: 9px 18px;
        border-radius: 8px;
        outline: none;
        text-decoration: none;
        border: none;
        color: aliceblue;
        background-color: #f10707;
        font-size: 18px;
        cursor: pointer;
        text-align: center;
    }
    .movieLinks > a:hover, .movieLinks > Link:hover{
        background-color: #2e2e2e
    }
    .movieLinks > a {
        margin-right: 30px;
    }



@media screen and (max-width: 740px){
    .movieInfoContainer{
        width: 90%;
    }
}

@media screen and (max-width: 600px){
    .movieInfoContainer{
        width: 95%;
    }
    .movieInfoCard{
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .movieInfoCardOne{
        width: 90%;
        padding: 0;
        border: none;
        margin: 0;
        height: 400px;
        margin-bottom: 40px;
    }   
        .movieInfoCardOne > img {
            border-top-right-radius: 40px;
            border-bottom-left-radius: 40px;
            
        }
    .movieInfoCardTwo{
        width: 90%;
    }
        .movieInfoCardTwo > h2 {
            margin-bottom: 20px;
            font-size: 18px;
        }
    
        .movieAbout{
            text-align: center;
            width: 90%;
            margin-left: auto;
            margin-right: auto;
        }
        .movieLinks{
            justify-content: space-between;
        }
        .movieLinks > a, .movieLinks > Link{
            font-size: 12px;
        }
        .movieLinks > a:hover, .movieLinks > Link:hover{
            background-color: #2e2e2e
        }
        .movieLinks > a {
            margin-right: 0;
        }
}

